import React from "react";
import { connect } from "react-redux";
import { Auth, DistrictList, FC_GetDistrictList } from "../../actions";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import TextInput from "../../components/Inputs/TextInput";
import Loading from "../../components/Loading/Loading";
import { StoreState } from "../../reducers";

// props for the component
interface AppProps {
  auth: Auth;
}

interface AppState {
  loading: boolean;
  error: {
    target: "main-error" | "main-success" | null;
    msg: string;
  };
  limit: number;
  page: number;
  documents: any[];
  districts: DistrictList[];
  total: number;
  selected_user: string;
  decision_residence: boolean | null;
  decision_disability: boolean | null;
  search_district: string;
  loading_districts: boolean;
  selected_district: DistrictList | null;
}

const defaultError = {
  target: null,
  msg: "",
};

class _ApproveDocuments extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      loading_districts: false,
      districts: [],
      search_district: "",
      error: defaultError,
      documents: [],
      limit: 10,
      page: 1,
      total: 0,
      selected_user: "",
      decision_disability: null,
      decision_residence: null,
      selected_district: null,
    };
  }

  componentDidMount() {
    FC_GetDistrictList((status, data, error) => {
      this.setState({
        districts: data,
        loading_districts: false,
        error:
          status === true
            ? defaultError
            : {
                target: "main-error",
                msg: `Districts: ${error}`,
              },
      });
    });
  }

  selectDistrict(district: DistrictList | null) {
    this.setState({
      selected_district: district,
    });
  }

  render() {
    if (this.state.selected_district === null) {
      return (
        <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
          <div className="p-2 flex items-center border-b">
            <div className="flex-1">
              <h2 className="text-2xl font-bold">District list</h2>
              <p className="text-gray-500">Select a district</p>
            </div>
            <div className="flex-1">
              <TextInput
                onChange={(e) =>
                  this.setState({
                    search_district: e.target.value,
                  })
                }
                value={this.state.search_district}
                error={""}
                label={""}
                placeholder={"Type a district"}
                name={""}
              />
            </div>
          </div>

          {this.state.loading_districts ? (
            <div>
              <Loading />
            </div>
          ) : (
            <table className="w-full border">
              <thead>
                <tr>
                  <th className="p-1 border">#</th>
                  <th className="p-1 border">District Name</th>
                  <th className="p-1 border">District Code</th>
                </tr>
              </thead>
              <tbody>
                {this.state.districts.length <= 0 ? (
                  <tr>
                    <td colSpan={5} className="py-4 text-center">
                      No data
                    </td>
                  </tr>
                ) : (
                  this.state.districts.map((el, i) => (
                    <tr
                      key={i}
                      className="hover:bg-accent-800 hover:bg-opacity-10 cursor-pointer"
                      onClick={this.selectDistrict.bind(this, el)}
                    >
                      <td className="p-1 border">{i + 1}</td>
                      <td className="p-1 border">{el.district_name}</td>
                      <td className="p-1 border">{el.district_code}</td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          )}
        </DashboardContainer>
      );
    }

    return (
      <div className="animate__animated animate__zoomIn animate__faster">
        <div className="container mx-auto px-2 flex items-center">
          <Button
            onClick={this.selectDistrict.bind(this, null)}
            theme="default"
          >
            Back
          </Button>
          <Heading className="flex-1 m-0">Approve document</Heading>
          <section className="text-center bg-accent-800 bg-opacity-20 text-accent-900 px-4 rounded">
            <span className="text-sm block text-gray-500 -mb-1">Total:</span>
            <h3 className="font-bold text-xl">12</h3>
          </section>
        </div>
        <DashboardContainer>Approve document</DashboardContainer>
      </div>
    );
  }
}

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const ApproveDocuments = connect(mapStateToProps, {})(_ApproveDocuments);
